@tailwind base;
@tailwind components;
@tailwind utilities;

:root{
  --sider-background-color: #fff0ec
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
li:hover span{
  color: #000;
}

.ant-layout .ant-layout-sider{
  background: var(--sider-background-color);
}
.ant-menu-dark{
  background: var(--sider-background-color);
  color: #000;
}
.ant-menu-dark.ant-menu-inline .ant-menu-sub.ant-menu-inline{
  background: var(--sider-background-color);
  color: #000;
}
.ant-menu-item .ant-menu-item-only-child:hover{
  color:#000;
}

h2{
  font-weight: bold;
  text-align: center;
  font-size: 20px;
  margin:0;
  margin-bottom: 10px;
  text-transform: uppercase;
}

table{
  width:100%;
  border-collapse: collapse;
}
th,td{
  padding: 14px 6px;
  border-bottom: 1px solid #CCC;
}
tbody tr:hover{
  background: #f2f2f2;
}
thead tr{
  background: #fff2e6;
}

.layout{
  height: 100vh;
}
.layout-content{
  overflow: auto;
  scroll-behavior: smooth;
}
.layout-footer{
  line-height: 30px;
  text-align: center;
  height:30px;
  padding:0;
}
.form-wrapper{
  width: 100%;
}

.product-thumbnail-list{
  width:6vh;
}

.image-preview{
  width:30%;
  margin: 10px auto;
}

.mobile-tb-col{
  display: none;
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .mobile-tb-col{
    display: table-cell;
  }

  .product-thumnail-list{
    width:6%;
  }
}
